<template>
    <div class="container_layout">
        <el-row class="guide_title">
            <el-col :span="16" :offset="4">請選擇走期天數？</el-col>
        </el-row>
       
        <el-row style="margin-top:40px;text-align:center;">
          <el-col :span="16" :offset="4" class="">

            <el-date-picker
            v-model="value2"
            popper-class="always_show"
            type="daterange"
            align="center"
            unlink-panels
            range-separator="~"
            start-placeholder="開始日期"
            end-placeholder="结束日期"
            :picker-options="pickerOptions">
            </el-date-picker>
            <div class="" style="
    color: #4da1ff;
    font-size: 18px;
    font-weight: bold;
    margin-top: 16px;margin-left:-8px;">*已選擇 14 天  </div>
          </el-col>
            
        </el-row>

        
    </div>
</template>
<script>
  export default {
    data() {
      return {
        pickerOptions: {
          shortcuts: [{
            text: '最近一周',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '最近一個月',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '最近三個月',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit('pick', [start, end]);
            }
          }]
        },
        value2: ''
      };
    }
  };
</script>
